.flex.container.project {
  width: 100px;
  margin: 10px;
  text-align: center;
  line-height: 75px;
  font-size: 30px;
  padding: 10px 10px 10px 10px;
  border:black;
  justify-content: space-around;
  display: flex;


}

.grid-item {
  border: 5px solid rgba(0, 0, 0, 0.2);
  grid-column: span 3;
  background-color: #a3989d; 
  color: #192231;
  min-height: 150px;
  display: flex;
  align-items: flex-end;
  padding: 0 0 20px 0;
  text-decoration: none;
  background-blend-mode: soft-light;
  background-size: 150%;
  transition: all 0.5s;  
  font-size: 1rem;     
  
}
.flex-container > div {
  
  width: 100px;
  margin: 10px;
  text-align: center;
  line-height: 75px;
  font-size: 30px;
}

/*grid child*/

.grid-item:first-child {
 grid-column: span 8;
 grid-row: span 2;  
}


.grid-item:hover {
background-color: transparent;
}

.grid-item div {
background-color: transparent;
padding: 8px 10px;
}
.grid-item h3 {
  font-weight: bold;
font-size: 1.5rem;

